"use client";

import { useState } from "react";
import * as Sentry from "@sentry/nextjs";

export default function Home() {
  const [a, setA] = useState(0);
  return (
    <p
      onClick={() => {
        setA((i) => i + 1);
        Sentry.captureException(new Error("LOLOL CLIENT"));
      }}
    >
      Hello Client! {a}
    </p>
  );
}
